import logo from './logo.svg';
import './App.css';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p onClick={generate}>
        I have edited my first component. Click on it to change the color!
        </p>
        <a
          className="App-link"
          href="https://en.wikipedia.org/wiki/Turtle"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn about turtles!
        </a>
      </header>
    </div>
  );
}
const generate = () => {
  const randomColor = Math.floor(Math.random()*10000000).toString(16);
  document.querySelector(".App-header").style.color = "#" + randomColor;
  console.log(randomColor);
}
export default App;
